.dashboard-container {
  display: flex;
  min-height: calc(100vh - 120px);
  background-color: #f5f5f7;

  .main-content {
    flex: 1;
    padding: 40px;
    transition: margin-left 0.3s ease;

    &.menu-open {
      margin-left: 280px;
    }

    .content-header {
      margin-bottom: 40px;

      .welcome-section {
        h1 {
          font-size: 32px;
          font-weight: 700;
          color: #1d1d1f;
          margin: 0 0 8px 0;
        }

        p {
          font-size: 18px;
          color: #86868b;
          margin: 0;
        }
      }
    }

    .dashboard-grid {
      display: grid;
      grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
      gap: 24px;

      .summary-card {
        border-radius: 20px;
        box-shadow: 0 4px 20px rgba(0,0,0,0.05);
        border: none;
        overflow: hidden;
        background: white;

        mat-card-header {
          padding: 24px;
          background: #fafafa;
          border-bottom: 1px solid #f0f0f0;

          mat-icon {
            color: #ff6b35;
          }

          mat-card-title {
            font-size: 18px;
            font-weight: 600;
          }
        }

        mat-card-content {
          padding: 24px;

          .info-item {
            display: flex;
            margin-bottom: 12px;
            font-size: 15px;

            .label {
              width: 80px;
              color: #86868b;
              font-weight: 500;
            }

            .value {
              color: #1d1d1f;
              font-weight: 600;
            }
          }

          .empty-state {
            text-align: center;
            padding: 20px 0;
            color: #86868b;

            p {
              margin-bottom: 16px;
            }
          }

          &.action-buttons {
            display: flex;
            flex-direction: column;
            gap: 12px;

            button {
              justify-content: flex-start;
              padding: 8px 16px;
              height: 48px;
              border-radius: 12px;

              mat-icon {
                margin-right: 8px;
              }
            }
          }
        }

        mat-card-actions {
          padding: 12px 24px;
          border-top: 1px solid #f0f0f0;
        }
      }
    }
  }
}

@media (max-width: 768px) {
  .dashboard-container {
    .main-content {
      padding: 20px;
      
      &.menu-open {
        margin-left: 0;
      }

      .dashboard-grid {
        grid-template-columns: 1fr;
      }
    }
  }
}
